import React, { useState, useEffect } from 'react'
import { Link } from 'gatsby'
import { debounce } from '@mui/material'
function getDocHeight() {
  var D = document;
  //cross browser competible way of getting the height of the scrollable view
  return Math.max(
      D.body.scrollHeight, D.documentElement.scrollHeight,
      D.body.offsetHeight, D.documentElement.offsetHeight,
      D.body.clientHeight, D.documentElement.clientHeight
  );
}
export default function Navbar ({
  sectionRefs = [],
  navbarItems = [],
  isSticky = false
}) {
  const [collapsed, setCollapsed] = useState(true)
  const [prevScrollPos, setPrevScrollPos] = useState(0)

  const [bg, setBackground] = useState('dark')
  const [activeSection, setActiveSection] = useState('home')


  const handleScroll = debounce(() => {
    const currentScrollPos = window.pageYOffset

    //check to see if we're within a certian range of the bottom of the screen
    //if we've reached the bottom override the selected value and set the background color to dark
    if ((window.innerHeight + window.scrollY + 100 ) >= getDocHeight()) {
      setBackground('dark')
      setActiveSection("contact-me");
      return;
    }
    //check which section we are on top of
    sectionRefs.forEach(function (item) {
      if (
        item.ref.current.getBoundingClientRect().top - 100 < 0 &&
        item.ref.current.getBoundingClientRect().bottom > 0
      ) {
        //set bg
        if (item.bg === 'dark') {
          setBackground('light')
        } else {
          setBackground('dark')
        }
        //set active section
        setActiveSection(item.ref.current.id)
      }
    })
    setPrevScrollPos(currentScrollPos);
  }, 100)

  useEffect(() => {
    window.addEventListener('scroll', handleScroll)

    return () => window.removeEventListener('scroll', handleScroll)
  }, [prevScrollPos, handleScroll])


  const renderHamburger = () => {
    return (
      <svg
        onClick={() => {
          setCollapsed(!collapsed)
        }}
        className='navbar__collpase__button'
        viewBox='0 0 100 80'
        width='20'
        height='20'
      >
        <rect width='100' height='20' rx='8'></rect>
        <rect y='35' width='100' height='20' rx='8'></rect>
        <rect y='70' width='100' height='20' rx='8'></rect>
      </svg>
    )
  }
  return (
    <div
      style={{
        position: isSticky ? 'sticky' : 'relative',
        transition: 'all 0.4s ease-in',
        top: '0',
        background: bg === 'light' ?  'var(--d-purple)' :'var(--d-black)',
        zIndex: 999,
        boxShadow:
          bg === 'light'
          ? '0 2px 20px  var(--d-purple)'
            : '0 2px 20px  var(--d-black)'
            
      }}
    >
      <nav className='navbar container'>
        <span className='logo'>
          <Link
            activeStyle={{ color: 'var(--cream)' }}
            style={{ color: 'var(--cream)' }}
            to='/'
          >
            Portfolio
          </Link>
        </span>
        <div className='navbar__collpase'>
          {renderHamburger()}
          <ul
            className={
              'navbar__collpase__list ' + (collapsed ? 'collapsed' : '')
            }
          >
            {navbarItems.map((item, index) => {
              return (
                <Link
                  className={
                    'navbar__collpase__list__link ' +
                    (item.name === activeSection ? 'active' : '')
                  }
                  to={'#' + item.name}
                  key={index + '_navbar_item'}
                >
                  {item.label}
                </Link>
              )
            })}
          </ul>
        </div>
      </nav>
    </div>
  )
}
