import { useForm } from '@formspree/react'; //ValidationError
import React, { useRef, useState } from 'react'
import Button from './Button'
import Input from './Input'
import SectionHeader from './SectionHeader'

export default function ContactForm ({ sectionRef }) {
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [msg, setMsg] = useState('')
  const [errors, setErrors] = useState(new Set())
  const [state, handleSubmit] = useForm('xpzkvozb')

  const formRef = useRef(null)
  const validateForm = () => {
    //check to see if there are any errors and fill the error set
    const newErrors = new Set()
    if (!name) {
      newErrors.add('name')
    }
    if (!email) {
      newErrors.add('email')
    }
    if (!msg) {
      newErrors.add('msg')
    }
    setErrors(newErrors)
    //return true if no errors found and subs form
    return newErrors.size === 0
  }
  const renderFeedback = (text, error = true) => {
    return (
      <p
        className='contact-form__form__element__feedback'
        style={{
          color: error ? '#ED5565' : '#609300'
        }}
      >
        {text}
      </p>
    )
  }

  return (
    <section ref={sectionRef} id='contact-me' className='section-spacing '>
      <SectionHeader title='Contact me' />
      <p className='contact-me__header'>Let’s talk about your project</p>
      <div className='contact-form'>
        {/* Borders */}

        {/* Form */}
        <form
          className='contact-form__form'
          onSubmit={handleSubmit}
          ref={formRef}
          id='contact-form-form'
        >
          <div className='contact-form__input-con'>
            <Input
              label='Name'
              value={name}
              name='name'
              id='name'
              onChange={e => setName(e.target.value)}
            />
            {errors.has('name') && renderFeedback('Enter your name')}
          </div>
          <div
            className='contact-form__input-con'
          >
              <Input
                label='Email'
                value={email}
                name='email'
                id='email'
                onChange={e => setEmail(e.target.value)}
              />
              {errors.has('email') && renderFeedback('Enter your email')}
              {state.errors.length > 0 &&
                state.errors[0].code === 'TYPE_EMAIL' &&
                renderFeedback('Enter a valid email')}
           
          </div>
          <div className='contact-form__input-con'>
            <Input
              label='Message'
              value={msg}
              onChange={e => setMsg(e.target.value)}
              name='comment'
              id='comment'
            />
            {errors.has('msg') && renderFeedback('Enter your message')}
          </div>
          {state.succeeded && renderFeedback("Thanks, I'll be in touch", false)}

          <Button
            containerStyle={{ marginTop: 60 }}
            text='Send It Over'
            onPress={e => {
              const formValid = validateForm()
              if (formValid)
                formRef.current.dispatchEvent(
                  new Event('submit', { cancelable: true, bubbles: true })
                )

              return
            }}
            disabled={state.submitting}
            loading={state.submitting}
          />
        </form>
        {/* info section */}
        <div className='contact-form__info-section'>
          <a
            href='mailto:amrirachid15@gmail.com'
            className='footer__element__icon-text'
          >
            <img src='../../images/mail.png' alt='mail to amrirachid15@gmail.com' />
            <p>amrirachid15@gmail.com</p>
          </a>
        
        </div>
      </div>
    </section>
  )
}
